<template>
  <view id="login" class="bg-white">
    <view class="login-inner">
      <view class="logo-area margin-normal">
        <view class="com-logo">
          <!-- <image style="width: 45px; height: 64px;" :src="'../../static/image/login/logo@2x.png'" /> -->
        </view>
        <view class="title-area">
          <view class="main"> 欢迎登录 </view>
          <view class="sub"> 海纳电商 </view>
        </view>
      </view>
      <view class="margin-normal">
        <u-form :model="form" ref="uForm">
          <view class="form-radius-wrapper">
            <u-form-item :border-bottom="false" label-width="auto">
              <u-input
                :placeholderStyle="placeholderStyle"
                placeholder="用户名"
                confirm-type="确定"
                v-model="form.userName"
              />
            </u-form-item>
          </view>

          <view class="form-radius-wrapper">
            <u-form-item :border-bottom="false" label-width="auto">
              <u-input
                :placeholderStyle="placeholderStyle"
                type="password"
                placeholder="密码"
                confirm-type="确定"
                password-icon
                @confirm="saveFormLogin"
                v-model="form.password"
              />
            </u-form-item>
          </view>
        </u-form>
        <view style="margin-top: 30px" class="radius-normal">
          <u-button
            ripple
            type="success"
            @click="saveFormLogin"
            :loading="loading"
            :disabled="disabled"
            >登录</u-button
          >
        </view>
        <view
          @click="gotoRegisterFn"
          class="text-right"
          style="margin-top: 20px"
        >
          <uni-text style="color: rgb(41, 121, 255)">
            <span class="font-size-small"
              >没有账号？点击此处注册</span
            ></uni-text
          >
        </view>
      </view>
    </view>
  </view>
</template>

<script>
export default {
  data() {
    return {
      placeholderStyle: "color: rgba(186,186,186,1);",
      form: {
        userName: "",
        password: "",
      },
      loading: false,
      disabled: false,
      timer: null,
    };
  },
  watch: {
    "form.userName": function (nV, oV) {
      this.disabled = !Boolean(this.form.password && nV);
    },
    "form.password": function (nV, oV) {
      this.disabled = !Boolean(
        this.form.userName && nV.length >= 6 && nV.length <= 12
      );
    },
  },
  onLoad() {},
  onShow() {
    const token = uni.getStorageSync("token");
    if (token) {
      uni.reLaunch({
        url: "/pages/home/index",
      });
    }

    this.getWifiName();

    // uni.getNetworkType({
    //   success: function (res) {
    //     console.log(res);

    //     uni.showToast({
    //       title: res.networkType,
    //       duration: 20000,
    //       icon: "none",
    //     });
    //   },
    // });
  },
  methods: {
    getWifiName: function () {
      var wifiManager, wifiInfo;
      var Context = plus.android.importClass("android.content.Context");
      var WifiManager = plus.android.importClass(
        "android.net.wifi.WifiManager"
      );
      var WifiInfo = plus.android.importClass("android.net.wifi.WifiInfo");
      wifiManager = plus.android
        .runtimeMainActivity()
        .getSystemService(Context.WIFI_SERVICE);
      wifiInfo = wifiManager.getConnectionInfo();
      var ssid = wifiInfo.getSSID() || "";
      if (ssid.length == 0) {
        return null;
      }
      //一些手机上获取SSID是有值的，但是实际IP为空，真实为未连接
      var i = parseInt(wifiInfo.getIpAddress());
      var ipStr =
        (i & 0xff) +
        "." +
        ((i >> 8) & 0xff) +
        "." +
        ((i >> 16) & 0xff) +
        "." +
        ((i >> 24) & 0xff);
      if (ipStr == "0.0.0.0") {
        console.log("NULL");
        return null;
      }

      if (ssid != "<unknown ssid>" && ssid.toUpperCase() != "0X") {
        console.log(ssid.replace(/\"/g, ""));
        return ssid.replace(/\"/g, "");
      }
      console.log("NULL");
      return null;
    },
    gotoRegisterFn() {
      uni.navigateTo({
        url: "/pages/register/index",
      });
    },
    saveFormLogin() {
      this.loading = this.disabled = true;
      this.$u
        .post("app/sys/login" + this.$u.queryParams(this.form), {})
        .then((res) => {
          console.log(res, "res");
          uni.setStorageSync("token", res.token);
          uni.reLaunch({
            url: "/pages/home/index",
          });
          this.loading = this.disabled = false;
        })
        .catch((err) => {
          this.loading = this.disabled = false;
        });
    },
  },
};
</script>
<style lang="scss">
#login {
  /*background-image: url("https://mp-1257924899.cos.ap-nanjing.myqcloud.com/nndt/login-main.png");*/
  background-image: url("../../static/image/login/backgroup@2x.png");
  background-repeat: no-repeat;
  background-size: contain;
  background-position: left bottom;
  height: 100%;
}
.login-inner {
  padding-top: 100px;
}
.com-logo {
  display: flex;
  align-items: center;
  & > image {
    width: 45px;
    height: 64px;
    margin-right: 10px;
  }
}
open-data {
  display: block;
  width: 200upx;
  height: 200upx;
  overflow: hidden;
  border-radius: 100upx;
  margin: 0 auto;
}
.form-radius-wrapper {
  background-color: #f5f5f8;
  overflow: hidden;
  border-radius: 16px;
  margin-top: 10px;
  padding: 0 10px;
}
.logo-area {
  display: flex;
  align-items: center;
  & > .title-area {
    color: rgba(42, 42, 47, 1);
    & > .main {
      height: 44px;
      font-size: 30px;
      font-weight: 800;
    }
    & > .sub {
      height: 34px;
      font-size: 23px;
      font-weight: 400;
    }
  }
}
</style>
